.person_container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.person_container h1 {
    margin-bottom: 1.2em;
}

.person_container p {
    font-size: 1.6em;
    margin-bottom: .7em;
}

.person_container p span {
    font-weight: bold;
}

.idade {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-columns: 300px 500px;

}

.numeros {
    display: flex;
}

.numeros p {
    margin-right: 1.3em;
}